import GameConfig from "../data/GameConfig";
import LayerManager from "../manager/LayerManager";

let _stage, _drawLayer, _this, _pattern, _clone;
let _color = GameConfig.DEFAULT_COLOR;
let _size = GameConfig.DEFAULT_LINE_SIZE;
let _opacity = GameConfig.DEFAULT_OPACITY;
let _screenToneType = 0;

export default class ScreenTone {

    init(stage) {

        GameConfig.CURRENT_TOOL = this;
        _stage = stage;
        _drawLayer = new Konva.Layer();
        _stage.add(_drawLayer);
        GameConfig.CURRENT_LAYER = _drawLayer;
        _this = this;

        this.useTool();
    }

    useTool () {

        let isDrawing = false;
        let currentLine;
        _pattern = this.getCrayonImage();

        _stage.on('mousedown touchstart', (evt) => {
            // if(!GameConfig.IS_DRAWING_MODE) return;
            // Start drawing
            isDrawing = true;

            let pos = this.getRelativePointerPosition(_stage);
            currentLine = {points:[pos.x, pos.y]}
            this.getSize();
            this.getColor();

        });

        _stage.on('mousemove touchmove', (evt) => {
            if (!isDrawing) return;

            let pos = this.getRelativePointerPosition(_stage);
            this.imageDraw(pos.x, pos.y);
            _drawLayer.batchDraw();

        });

        _stage.on('mouseup touchend contentTouchend', (evt) => {
            // End drawing
            // isDrawing = false;
            // LayerManager.prototype.init(_drawLayer);
            // _drawLayer = new Konva.Layer();
            // _stage.add(_drawLayer);
            // GameConfig.CURRENT_LAYER = _drawLayer;
        });
    }

    getRelativePointerPosition(node) {
        // the function will return pointer position relative to the passed node
        let transform = node.getAbsoluteTransform().copy();
        // to detect relative position we need to invert transform
        // transform.invert();

        // get pointer (say mouse or touch) position
        let pos = node.getStage().getPointerPosition();

        // now we find relative point
        return transform.point(pos);
    }


    imageDraw(x, y) {

        return;
        // let obj = this.getCrayonImage();

        _clone = _pattern.clone({
            x:x,
            y:y,

        });
        _clone.cache();
        _drawLayer.add(_clone);
    }

    destroy () {
        LayerManager.prototype.init(_drawLayer);
        if(_stage)_stage.off('mousedown touchstart');
        if(_stage)_stage.off('mousemove touchmove');
        if(_stage)_stage.off('mouseup touchend contentTouchend');
    }

    /**
     *
     * @param color
     */
    setColor(color) { _color = color;}
    getColor() { return _color;}

    /**
     *
     * @param size
     */
    setSize(size) { _size = size;}
    getSize() { return _size;}

    /**
     *
     * @param opacity
     */
    setOpacity(opacity) { _opacity = opacity;}
    getOpacity() { return _opacity;}

    /**
     *
     * @param lineType
     */
    setLineType(e) {
        let type = e.target.id.substr(1, e.target.name.length + 1);
        // console.log(type)
        switch (type)
        {
            case 'sA' :
                _screenToneType = 0;
                break;
            case 'sB' :
                _screenToneType = 1;
                break;
            case 'sC' :
                _screenToneType = 2
                break;
            default :
                _screenToneType = 0;
                break;
        }
        _pattern = this.getCrayonImage();
    }

    getLineType() {return _screenToneType;}

    getCrayonImage() {
        // tempBmd = new Konva.B(7,7,true,0x000000)
        _pattern = new Konva.Image();
        for(let i =0; i<4; i++)
        {
            let rect = new Konva.Rect({
                x: i,
                y: i,
                width:1,
                height:1,
                fill:'#fa0a8e',

            });
            _pattern.fillPatternImage = rect;
        }
        this.sample();
        return _pattern;

    }

    sample() {
       /* const image = new window.Image();
        image.onload = () => {
            this.setState({
                fillPatternImage: image
            });
        }
        image.src = 'http://i.imgur.com/A6H6xHF.png';
        this.state = {
            color: 'green',
            fillPatternImage: null
        };*/

        _pattern = new Image();
        _pattern.onload =()=> {
            let img = new Konva.Image({
                image:_pattern,
                width:20,
                height:20
            });
            // _pattern = img;
            // _pattern.cache();
            this.patternDraw(_pattern);
        }
        _pattern.src = 'asset/image/screenToneDefault.png';
        // _pattern.src = 'http://i.imgur.com/A6H6xHF.png';



    }

    patternDraw(pattern) {
        let images = {};
        console.log(_pattern)
        let patternPentagon = new Konva.Rect({
            x: 0,
            y: 0,
            width: 800,
            height: 550,
            // sides: 6,
            // radius: 170,
            fillPatternImage: pattern,
            fillPatternOffset: { x:20, y: 20 },
            // stroke: 'black',
            // strokeWidth: 4,
            // draggable: true,
        });

        // patternPentagon.fillPatternImage(_pattern);
        _drawLayer.add(patternPentagon);
        _drawLayer.draw();

        let sources = {
            darthVader: 'asset/image/screenToneDefault.png',
        };

    }

}


